.navbar-container {
    display: flex;
    flex-direction: column;
}
.header-nav-area {
    border-top: 10px solid;
    border-image-slice: 1;
    border-width: 2px;
    @media (max-width: 1199px) {
        display: none;
    }
}

.border-gradient {
    border-image-source: linear-gradient(to left, #d53a9c00, #727272, #d53a9c00);
}

.header-container {
    min-height: 100px;
    background-color: transparent;
}
.site-logo {
    padding: 20px 0;
    & img {
        height: auto;
        width: auto;
        filter: invert(100%);
    }
}

#menu-icons-menu {
    align-items: center;
    a {
        color: gray !important;
        &:hover {
            color: $white !important;
        }
    }
}
#primary-menu {
    a {
        word-spacing: 10px;
    }
}
.navbar {
    .navbar-nav {
        .nav-item {
            padding: 0px;
            margin-left: 12px !important;
            min-height: 10px !important;
            color: $white;
            a {
                color: $white;
                font-size: 1.025rem !important;
                font-weight: $font-weight-normal !important;
            }
            &.active {
                a {
                    color: $dark-gray;
                    font-weight: $font-weight-medium;
                }
            }
            a:hover {
                color: $purple;
            }
            @media (max-width: 1199px) {
                font-size: $font-size-sm !important;
                font-weight: $font-weight-semibold !important;
                padding-bottom: 5px !important;
                padding-top: 5px !important;
                margin-left: 0px !important;
                margin-right: 0px !important;
                width: 100%;
            }
        }
    }
}

@media (max-width: 1199px) {
    .nav-container {
        display: none;
    }
    .header-widget-area {
        display: none;
    }
    .navbar-toggler:focus {
        box-shadow: unset !important;
    }
    .custom-toggler .navbar-toggler-icon {
        box-shadow: unset !important;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0,0,0, 0.5)' stroke-width='3' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
    }
    .custom-toggler:active .navbar-toggler-icon:active {
        box-shadow: unset !important;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(147,66,40, 0.5)' stroke-width='3' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
    }

    .header-nav-area {
        padding: 0px !important;

        .row {
            padding: 0px !important;
        }

        .navbar {
            padding: 0px !important;
        }
    }
}
