.about {
    .row {
        display: flex;
        align-items: center;
        justify-content: end;
    }
    @media (max-width: 599px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        .row {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}
.about-text {
    color: $dark-purple;
}
.box-square,
.box-rect {
    height: 200px;
    width: 300px;
    background-position: center;
    border-radius: 5px;
    margin: 10px;
    @media (max-width: 1199px) {
        margin: 2px;
    }
    @media (max-width: 500px) {
        width: 300px;
        margin: 5px;
    }
    &:hover {
        border: solid 2px rgb(25, 105, 255);
    }
}
.box-rect {
    height: 200px;
    width: 200px;
}
.box-1 {
    background-image: url("../../../assets/img/about-1.jpg");
}
.box-2 {
    background-image: url("../../../assets/img/about-2.png");
}
.box-3 {
    background-image: url("../../../assets/img/about-3.png");
}
.box-4 {
    background-image: url("../../../assets/img/about-4.png");
}
