form {
    align-items: flex-end;
    @media (max-width: 576px) {
        flex-direction: column;
        align-items: flex-start;
    }
}

.form-button {
    width: 200px;
    height: 39px;
    background-color: $purple;
    color: white;
    border: none;
    @media (max-width: 576px) {
        margin-top: 22px;
    }
}
